export default {
  name: 'HeroesPanel',
  props: ['dataList', 'selected'],
  data() {
    return {
      selectedFaction: 'Druid'
    }
  },
  methods: {
    itemClick(item) {
      this.selectedFaction = item.id
      this.$emit('itemClick', item)
    }
  },
  watch: {
    selected(val) {
      this.selectedFaction = val
    }
  }
}
